/* Custom styling overrides for my site */


/* Increase spacing between sections */
.section {
 margin-top: 5rem;
 margin-bottom: 5rem;
}

/* Increase spacing between course bullets */
#teaching-section ul li {
 margin-bottom: 0.6rem;
 line-height: 1.7;
}

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap');
body {
 font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
 font-weight: 400;
 line-height: 1.7;
 color: #1a1a1a; /* calm dark gray text */
}

/* Headings slightly bolder */
h1, h2, h3, h4, h5, h6 {
 font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
 font-weight: 700;
 letter-spacing: 0.02em;
 color: #111;
}

p {
 font-size: 1.05rem;
 margin-bottom: 1.1rem;
}

li {
 font-size: 1.05rem;
}

h1 { font-size: 2.2rem; }
h2 { font-size: 1.7rem; margin-top: 2.5rem; }
